/* src/components/AdminPanel.css */
.admin-panel {
  background: url('../../public/background.jpg') no-repeat center center;
  background-size: cover;
  color: white;
  min-height: 100vh;
  padding: 80px 20px 40px;
  position: relative;
  font-family: 'AtomicAlice', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Добавить в AdminPanel.css */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
  color: #666;
}

.loading-spinner {
  font-size: 3em;
  margin-bottom: 20px;
  animation: spin 1.5s linear infinite;
}

.loading-text {
  font-size: 1.2em;
}

/* Контейнер для центрирования всего контента */
.admin-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
}

/* Стили для кнопок действий */
.agents-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 25px;
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.delete-btn {
  background: #ff4757;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s ease;
  min-height: 40px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.delete-btn:hover {
  background: #ff3742;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(255, 71, 87, 0.3);
}

.delete-btn:active {
  transform: translateY(0);
}

/* Карточка агента */
.agent-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 25px;
  transition: all 0.3s ease;
  position: relative;
  width: 100%;
  max-width: 400px;
  flex: 0 0 auto;
}

.agent-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 255, 255, 0.4);
}

.logout-btn-header.with-icon {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
}

.logout-btn-header.with-icon .icon {
  font-size: 16px;
  transition: transform 0.3s ease;
}

.logout-btn-header.with-icon:hover .icon {
  transform: translateX(2px);
}

.logout-btn-header {
  background: linear-gradient(135deg, #ff6b6b 0%, #ee5a52 100%);
  color: white;
  border: none;
  border-radius: 25px;
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  gap: 8px;
  min-width: 120px;
  justify-content: center;
  height: 48px;
  align-self: center;
}

.logout-btn-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.logout-btn-header:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
  background: linear-gradient(135deg, #ff5252 0%, #e53935 100%);
}

.logout-btn-header:hover::before {
  left: 100%;
}

.logout-btn-header:active {
  transform: translateY(0);
  box-shadow: 0 2px 10px rgba(255, 107, 107, 0.3);
}

.logout-btn-header:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 107, 107, 0.3), 0 4px 15px rgba(255, 107, 107, 0.3);
}

/* Альтернативный вариант с иконкой */
.logout-btn-header::after {
  content: '🚪';
  font-size: 16px;
  transition: transform 0.3s ease;
}

.logout-btn-header:hover::after {
  transform: translateX(2px);
}

/* Вариант для темной темы */
@media (prefers-color-scheme: dark) {
  .logout-btn-header {
    background: linear-gradient(135deg, #ff5252 0%, #d32f2f 100%);
    box-shadow: 0 4px 15px rgba(255, 82, 82, 0.3);
  }
}

/* Адаптивность для мобильных */
@media (max-width: 768px) {
  .logout-btn-header {
    padding: 10px 20px;
    font-size: 13px;
    min-width: 100px;
    border-radius: 20px;
    height: 44px;
  }
  
  .logout-btn-header::after {
    font-size: 14px;
  }
}

/* Дополнительные варианты стилей */
.logout-btn-header.minimal:hover {
  background: #ff6b6b;
  color: white;
  transform: none;
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
}

/* Стиль с иконкой слева */
.logout-btn-header.icon-left {
  flex-direction: row-reverse;
}

.logout-btn-header.icon-left::after {
  margin-right: 8px;
}

/* Анимация загрузки при выходе */
.logout-btn-header.loading {
  pointer-events: none;
  opacity: 0.7;
}

.logout-btn-header.loading::after {
  content: '⏳';
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Убедитесь, что нет перекрывающих элементов */
.agent-card * {
  position: relative;
  z-index: 1;
}

.no-agents-hint {
  margin-top: 10px;
  color: #888;
  font-size: 0.9em;
}

/* Заголовок и статистика */
.admin-header {
  text-align: center;
  margin-bottom: 40px;
  width: 100%;
}

.admin-title {
  font-size: 2.5em;
  font-weight: 700;
  margin-bottom: 30px;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

.admin-stats {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
  margin-bottom: 30px;
  width: 100%;
}

.stat-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 20px;
  padding: 25px 30px;
  text-align: center;
  min-width: 150px;
  transition: all 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
}

.stat-value {
  font-size: 2.5em;
  font-weight: 700;
  margin-bottom: 8px;
  color: #00ff7f;
}

.stat-label {
  font-size: 1em;
  font-weight: 500;
  opacity: 0.9;
}

/* Элементы управления */
.admin-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  flex-wrap: wrap;
  width: 100%;
  max-width: 800px;
}

.search-container {
  flex: 1;
  max-width: 500px;
  min-width: 300px;
}

.search-input {
  width: 100%;
  padding: 18px 25px;
  border-radius: 15px;
  border: none;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  color: white;
  font-size: 1.1em;
  font-weight: 500;
  outline: none;
  border: 2px solid rgba(255, 255, 255, 0.2);
  font-family: 'AtomicAlice', sans-serif;
  transition: all 0.3s ease;
  height: 48px;
  box-sizing: border-box;
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.search-input:focus {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 15px rgba(0, 230, 255, 0.3);
}

/* Контейнер для кнопки выхода */
.logout-container {
  display: flex;
  align-items: center;
}

.agent-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.agent-name {
  font-size: 1.4em;
  font-weight: 700;
  color: white;
  flex: 1;
  margin-right: 15px;
}

.status-badge {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.8em;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  white-space: nowrap;
}

.status-active {
  background: rgba(0, 255, 127, 0.2);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.5);
}

.status-inactive {
  background: rgba(255, 68, 68, 0.2);
  color: #ff4444;
  border: 1px solid rgba(255, 68, 68, 0.5);
}

.status-maintenance {
  background: rgba(255, 193, 7, 0.2);
  color: #ffc107;
  border: 1px solid rgba(255, 193, 7, 0.5);
}

.status-unknown {
  background: rgba(158, 158, 158, 0.2);
  color: #9e9e9e;
  border: 1px solid rgba(158, 158, 158, 0.5);
}

.agent-info {
  margin-bottom: 20px;
}

.info-row {
  display: flex;
  margin-bottom: 10px;
  align-items: flex-start;
}

.info-label {
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  min-width: 160px;
  font-size: 0.95em;
}

.info-value {
  flex: 1;
  color: white;
  font-weight: 500;
  word-break: break-word;
}

.agent-actions {
  display: flex;
  justify-content: flex-end;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 20px;
}

.delete-btn {
  background: rgba(255, 68, 68, 0.2);
  color: #ff4444;
  border: 2px solid rgba(255, 68, 68, 0.5);
  padding: 10px 20px;
  border-radius: 10px;
  font-family: 'AtomicAlice', sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.95em;
}

.delete-btn:hover {
  background: rgba(255, 68, 68, 0.3);
  border-color: rgba(255, 68, 68, 0.8);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255, 68, 68, 0.3);
}

/* Состояние без агентов */
.no-agents {
  text-align: center;
  padding: 60px 20px;
  width: 100%;
}

.no-agents-icon {
  font-size: 4em;
  margin-bottom: 20px;
  opacity: 0.7;
}

.no-agents-text {
  font-size: 1.5em;
  font-weight: 600;
  opacity: 0.8;
}

/* Адаптивность */
@media (max-width: 768px) {
  .admin-panel {
    padding: 60px 15px 30px;
  }

  .admin-title {
    font-size: 2em;
  }

  .admin-stats {
    gap: 15px;
  }

  .stat-card {
    padding: 20px;
    min-width: 120px;
  }

  .stat-value {
    font-size: 2em;
  }

  .admin-controls {
    flex-direction: column;
    gap: 15px;
  }

  .search-container {
    min-width: 100%;
    max-width: 100%;
  }

  .logout-container {
    width: 100%;
    justify-content: center;
  }

  .logout-btn-header {
    width: 100%;
    max-width: 200px;
  }

  .agents-list {
    padding: 0 15px;
    gap: 20px;
  }

  .agent-card {
    max-width: 100%;
    min-width: 300px;
  }

  .agent-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .agent-name {
    margin-right: 0;
  }

  .info-row {
    flex-direction: column;
    gap: 5px;
  }

  .info-label {
    min-width: auto;
  }
}

@media (max-width: 480px) {
  .admin-panel {
    padding: 50px 10px 20px;
  }

  .admin-title {
    font-size: 1.7em;
  }

  .admin-stats {
    flex-direction: column;
    align-items: center;
  }

  .stat-card {
    width: 100%;
    max-width: 200px;
  }

  .search-input {
    padding: 15px 20px;
    font-size: 1em;
    height: 44px;
  }

  .logout-btn-header {
    height: 44px;
    font-size: 12px;
  }

  .agents-list {
    padding: 0 10px;
    gap: 15px;
  }

  .agent-card {
    padding: 15px;
    min-width: 280px;
  }

  .delete-btn {
    width: 100%;
    padding: 12px;
  }
}
.admin-panel {
  background: url('../../public/background.jpg') no-repeat center center;
  background-size: cover;
  background-color: #000; /* Фолбэк */
  color: white;
  min-height: 100vh;
  padding: 80px 20px 40px;
  font-family: 'AtomicAlice', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.admin-panel::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: url('../../public/background.jpg') no-repeat center center;
  background-size: cover;
  background-attachment: fixed;
  z-index: -1;
}
/* Импорт шрифтов и общих стилей из TargetChecker.css */
@import './TargetChecker.css';